@import url(https://fonts.googleapis.com/css2?family=Lobster&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@300&display=swap);
html{
    overflow: hidden;
    width: 100%;
    height: 100%;
    background: rgb(255, 255, 255);
}

body{
    font-family: "Nunito";
    width: 100%;
    height: 100%;
    margin: 0px;
}

#root{
    width: 100%;
    height: 100%;
    padding-left: 0;
}

#afterRoot{
    padding: 0px;
    margin: 0px;
    width:100%;
    height: 100%;
    text-align: center;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    -webkit-align-items: center;
            align-items: center;
    border: 0;
}

#drum-machine{
    border-color: tomato;
    border-style: solid;
    padding: 0px 4vw;
    margin-left: 0px;
    height: 75vh;
    border-radius: 4px;
    background-color: rgb(255, 255, 255);
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
}

#pads{
    margin-left: 0px;
    padding: 20px;
    background:rgb(255, 255, 255);
    display: grid;
    grid-template-columns: auto auto auto;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}


.drum-pad{
    margin-left: 0px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    width: 3vw;
    height: 3vw;
    color: white;
    font-size: 2em;
    background: rgb(235, 78, 78);
    padding: 4vh;
    border-radius: 4px;
    margin: 5px;
    cursor: pointer;
}
#displaydiv{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    margin-left: 0px;

}

#display{
    margin-left: 0px;
    width: 100%;
    height: 6vh;
    color: white;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    text-align: center;
    border-radius: 4px;
    background: rgb(235, 78, 78);
}

#title1{
    font-family: 'Lobster';
    color: #ec7b4e;
    font-size: 64px;
}

@media screen and (max-width: 600px){
    html{
        overflow: hidden;
    }
    #drum-machine{
        overflow: hidden;
        width: 100vw;
    }
}
